.App {
  text-align: center;
}

.App-logo {
  height: 20%;
  width: auto;
  pointer-events: none;
}

.Resize {
  height: 30%;
  width: auto;
  pointer-events: none;
}

.Jevil1 {
  color: "#333333";
  background: url("/anything.png") no-repeat scroll 0 0 transparent;
  height: 1000px;
}

.Jevil2 {
  color: "#333333";
  background: url("/anything.png") no-repeat scroll 0 0 transparent;
}


#Applink {
  margin-top: 132px;
  font-size: 60px;
  font-weight: 550;
  color: #909090;
  text-decoration: none;
  margin-bottom: 250px
}

#Applink:hover {
  color: #DDDDDD;
}

.App-link {
  color: #909090;
}

.App-link:hover {
  color: #DDDDDD;
}

.Jevil0, .Jevil1, .Jevil2, .Jevil3, .Jevil4, .Jevil5 {
  height: 1000px;
  width: auto
}

.Jevil0 {
  display: flex;
  margin-top: -600px;
  margin-left: -420px;
  align-items: center;
}
.Jevil1 {
  display: grid;
  margin-top: -1050px;
  margin-left: 1100px;
  align-items: center;
  width: auto;
}
.Jevil2 {
  margin-top: -250px;
  margin-left: 1100px;
  align-items: center;
}
.Jevil3 {
  display: flex;
  margin-top: -1200px;
  margin-left: -220px;
  align-items: center;
}
.Jevil4 {
  display: grid;
  margin-top: -1550px;
  margin-left: 400px;
  align-items: center;
}
.Jevil5 {
  margin-top: -250px;
  margin-left: 600px;
  align-items: center;
}

@media (prefers-reduced-motion: no-preference) {
  .Jevil0 {
    animation: App-logo-spin0 infinite 1s linear;
  }
  .Jevil1 {
    animation: App-logo-spin0 infinite 1.5s linear;
  }
  .Jevil2 {
    animation: App-logo-spin0 infinite 1s linear;

  }
  .Jevil3 {
    animation: App-logo-spin1 infinite 1.2s linear;

  }
  .Jevil4 {
    animation: App-logo-spin1 infinite 1s linear;
  }
  .Jevil5 {
    animation: App-logo-spin1 infinite 0.8s linear;
  }
}

.App-header {
  background-color: #730073;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin0 {
  10% {
    transform: rotate(20deg);
  }
  25% {
    transform: rotate(-20deg);
  }
  50% {
    transform: rotate(0deg);
  }
  60% {
    transform: rotate(20deg);
  }
  75% {
    transform: rotate(-20deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@keyframes App-logo-spin1 {
  10% {
    transform: rotate(-20deg);
  }
  25% {
    transform: rotate(40deg);
  }
  50% {
    transform: rotate(0deg);
  }
  60% {
    transform: rotate(-20deg);
  }
  75% {
    transform: rotate(40deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
